<script>
// @ is an alias to /src
import Form from "../components/Form";

export default {
  name: "Contact",
  components: {
    Form,
  },

  data: () => ({
    windowWidth: (window.innerWidth - 100) / 2.5,
    windowWidthPhone: window.innerWidth - 100,
    submitLocal: sessionStorage["submitted"],
  }),

  metaInfo: {
    title: "Shami Derma | Contact Us",
    // meta: [{ name: "description", content: "Ultherapy Description" }],
  },

  methods: {
    getWidthMap() {
      if (window.innerWidth > 1000) {
        return this.windowWidth;
      } else {
        return "";
      }
    },
    getFormWidth() {
      if (window.innerWidth > 1000) {
        return "center1";
      } else {
        return "";
      }
    },
    getFormWidth2() {
      if (window.innerWidth > 1000) {
        return "center2";
      } else {
        return "center1";
      }
    },
    getMapDivClass() {
      if (window.innerWidth > 1000) {
        return "center4 transparent";
      } else {
        return "center3 transparent";
      }
    },
  },
};
</script>
